body {
    padding: 0;
    margin: 0 0;
    font-family: sans-serif;
  }
.navbar li {
  display: inline-block;
  text-align: center;
}

.itemLeft {
  width: 10%;
}

.itemCenter {
	width: 75%;
}

.itemRight {
  width: 15%;
  
}

.navbar {
  background: rgb(54, 193, 240);
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

li {
  color: rgb(228, 255, 73);
  padding: 5px 0;
}

body {
  font-size: 20px;
}

.header{
	background-color: lightblue;
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 75px;
	font-weight: bold;
	padding: 10px 0 10px 0; 
}

h2{
	text-align: center;
	font-size: 20px;
	padding-bottom: 10px;
}

.card {
    width: 200px;
    height: 150px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }
  
.footer{
	background-color: black;
	width: 100%;
	position: relative;
}

.footer p{
	color: white;
	text-align: center;
	padding: 10px 0;
	font-size: 15px;
	font-style: italic;
}
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}
